round-image: linear-gradient(to right, #ccc, #095484, #ccc);\n}\n\n.list-devider {\n  width: 80%;\n  margin-top: 0.25rem;\n  margin-bottom: 0.25rem;\n}\n\n.clickable-card:hover {\n  background-color: rgba(0, 0, 0, 0.075);\n}\n\n.card label,\n.list-body-fields label,\n.field-label {\n  font-weight: bold;\n}\n\n.card-action-icons {\n  text-align: right;\n  bottom: 0px;\n  right: 0.5rem;\n\n  width: 10rem;\n  margin-left: auto;\n}\n\ntd input,\n.card-action-icons input {\n  margin-left: 0.5rem;\n  margin-right: 0.5rem;\n}\n\ntd a i,\n.card-action-icons a i {\n  color: rgb(196, 191, 191);\n  margin-left: 0.5rem;\n  margin-right: 0.5rem;\n}\n\n/*change the icon color when card is hoverred*/\n\ntr:hover td a i,\n.clickable-card:hover .card-action-icons a i {\n  color: black;\n}\n\ntr:hover td i.fa-edit:hover,\n.clickable-card:hover .card-action-icons i.fa-edit:hover {\n  color: blue;\n}\n\ntr:hover td i.fa-trash-alt:hover,\n.clickable-card:hover .card-action-icons i.fa-trash-alt:hover {\n  color: red;\n}\n\n/* action buttons and paginations\n*/\n\n.action-buttons {\n  width: 100%;\n  float: right;\n  text-align: right;\n}\n\n.action-buttons .btn {\n  display: inline-block;\n  margin-left: 1rem;\n}\n\n.pagination li {\n  min-width: 2.5rem;\n}\n\n.total_counts {\n  padding-top: 0.8rem;\n  text-align: right;\n}\n\n.page-link {\n  padding: 0.25rem 0.5rem;\n  text-align: center;\n  font-size: 0.8rem;\n}\n\n.page-control {\n  display: inline-block;\n  width: 3rem;\n  height: 1.5rem;\n  font-size: 0.8rem;\n  padding: 0 0.25rem;\n}\n\n/* earch area\n*/\n\n::-ms-clear {\n  display: none;\n}\n\n.icon-clear {\n  cursor: pointer;\n  margin-top: 0.5rem;\n}\n\n.icon-clear-inside {\n  z-index: 3;\n  vertical-align: middle;\n  padding: 0.5rem 0.5rem 0rem 0.5rem;\n  cursor: pointer;\n  position: relative;\n  margin-left: -2rem; /* text 1 + left 0.5 + right 0.5 = 2 rem*/\n  border: 0;\n}\n\n.icon-clear-badge {\n  cursor: pointer;\n  margin-left: 1rem;\n}\n\n.pointer {\n  cursor: pointer;\n}\n\n.ref-badge {\n  border: 1px solid #eceff3; /*rgba(0,0,0,.125); */\n  background-color: #eceff3;\n}\n\n.refine-search-text {\n  font-size: 0.8rem; /*rgba(0,0,0,.125); */\n  color: #868181;\n}\n\n/* pop-up models\n*/\n\n.select-model {\n  display: block;\n  position: fixed;\n  z-index: 1000;\n  padding-top: 100px;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n  background-color: rgb(0, 0, 0);\n  background-color: rgba(0, 0, 0, 0.4);\n}\n\n.select-model.show {\n  display: block;\n}\n\n.select-model-content {\n  position: relative;\n  float: right;\n  background-color: #fefefe;\n  /*margin: auto;*/\n  margin-right: 50px;\n  padding: 0;\n  border: 1px solid #888;\n  width: 60%;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\n}\n\n.single-line-edit {\n}\n\n.single-line-edit > .row {\n  padding-bottom: 2px;\n}\n\n/*NdbDatePicker Range Selector */\n\n.custom-day {\n  text-align: center;\n  padding: 0.185rem 0.25rem;\n  display: inline-block;\n  height: 2rem;\n  width: 2rem;\n}\n\n.custom-day.focused {\n  background-color: #e6e6e6;\n}\n\n.custom-day.range,\n.custom-day:hover {\n  background-color: rgb(2, 117, 216);\n  color: white;\n}\n\n.custom-day.faded {\n  background-color: rgba(2, 117, 216, 0.5);\n}\n\n.date-selection-pop {\n  position: absolute;\n  top: 100%;\n  left: 0;\n  z-index: 1000;\n  display: block;\n  float: left;\n  min-width: 10rem;\n  /*padding:.5rem 0;*/\n  margin: 0.125rem 0 0;\n  font-size: 1rem;\n  color: #212529;\n  text-align: left;\n  list-style: none;\n  background-color: #fff;\n  background-clip: padding-box;\n  border: 1px solid #dfdfdf;\n  border-radius: 0.25rem;\n}\n\n.add-area {\n  margin-left: 0.75rem;\n  margin-top: 1rem;\n}\n\n.badge-click {\n  cursor: pointer;\n}\n\ndiv.badge-click:hover {\n  background-color: #495057 !important;\n  text-decoration: underline;\n}\n\n/* Grid/List view filter\n*/\n\n.view-radio + label {\n  transition: all 0.2s ease-in-out;\n  color: #bbbdbf