\n  cursor: pointer;\n}\n\n.nohref-link:hover {\n  color: #0056b3 !important;\n}\n\n.nohref-link.active,\n.nohref-link.active:hover {\n  color: #fff !important;\n}\n\n.list-check-box {\n  zoom: 1.1;\n}\n\n.currency {\n  margin-right: 10px;\n}\n\n/*Start Archive Slider*/\n\n/* The switch - the box around the slider */\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 60px;\n  height: 24px;\n}\n\n/* Hide default HTML checkbox */\n\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n/* The slider */\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  transition: 0.4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: '';\n  height: 16px;\n  width: 16px;\n  left: 10px;\n  bottom: 4px;\n  background-color: white;\n  transition: 0.4s;\n}\n\ninput:checked + .slider {\n  background-color: #2196f3;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px #2196f3;\n}\n\ninput:checked + .slider:before {\n  transform: translateX(26px);\n}\n\n/* Rounded sliders */\n\n.slider.round {\n  border-radius: 24px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n/*End Archive Slider*/\n\n.badge-group {\n  min-height: 1.5rem;\n}\n\n.new-modal {\n  z-index: 1100;\n  display: block;\n  padding-top: 0px;\n  position: fixed;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n  background-color: rgba(0, 0, 0, 0.2);\n}\n\n.new-card {\n  width: 400px;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.available-hints {\n  padding: 5px 0 5px 20px;\n  background-color: #0000000d;\n}\n\n.col-gutter {\n}\n\n@media screen and (max-width: 500px) {\n  .col-gutter > [class*=\"col-\"] {\n    padding-right: 5px;\n    padding-left: 5px;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"